<template>
    <div class="errorfooter">
        <div @click="emptyError" class="test-push">
            <i class="fa fa-trash-o"></i>
            移除
        </div>

        <div class="true-or-flase">
            <span class="icon-true"><i class="fa fa-check-circle"></i>
                {{AnswerGroups.Answertrue}}
            </span>
            <span class="icon-false"><i class="fa fa-times-circle"></i>
                {{AnswerGroups.Answerfalse}}
            </span>
        </div>
        <div class="num">
            <i class="fa fa-th-large"></i>
            <span>{{AnswerGroups.Answerno}}</span>/{{AnswerGroups.ErrorNum}}</div>
    </div>
</template>

<script>
    export default {
        name: 'errorfooter',
        props: ['AnswerGroups'],
        methods: {
            emptyError() {
                //获取本地储存错误题目信息
                let ErrorBookArr = window.localStorage.ErrorBook
                let newErrorBookArr = [];

                for (let index = 0; index < ErrorBookArr.split(',').length; index++) {
                    newErrorBookArr.push(ErrorBookArr.split(',')[index])
                }
                //获取当前错误题目ID
                let thisErrorIndex = newErrorBookArr[[this.AnswerGroups.Answerno] - this.AnswerGroups.Answertrue]
                //删除一个错题
                newErrorBookArr.splice(newErrorBookArr.indexOf(thisErrorIndex), 1)
                //将删除的数据进行一个本地储存
                window.localStorage.ErrorBook = newErrorBookArr.toString();
                //每删除一个数据就要删除一个本地题目错误次数
                window.localStorage.ErrorNum = Number(window.localStorage.ErrorNum) - 1
                this.$emit('changeAnswerGroups', 'Answerno')

                if (this.AnswerGroups.Answerno == this.AnswerGroups.ErrorNum) {
                    this.$router.push('/errorbook')
                    return
                }
            },
        },
        data() {
            return {
                ErrorBookData: window.localStorage.ErrorBook.split(','),
            }
        },
    }
</script>

<style scoped lang="less">
    .errorfooter {
        margin-top: 5rem;
        border-top: 0.3rem solid lightgray;
        display: flex;
        position:fixed;
        width: 100%;
        bottom: 0;
        padding: 1.2rem 0;
        text-align: center;
        font-size: 1.3rem;
        background: white;
        font-weight: 400;
        hr {}

        .test-push {
            i {
                color: black;
            }
            color: green;
            flex: 100%;
        }

        .shoucan {
            flex: 100%;
            i {
                &.active {
                    color: red;
                }
            }
        }

        .true-or-flase {
            span {
                margin: 0 0.5rem;
            }

            .icon-true {
                color: green;
            }

            .icon-false {
                color: red;
            }

            flex: 100%;

        }

        .num {
            flex: 100%;

        }
    }
</style>