<template>
    <div class="errortestcontenttest">
        <!-- {{ErrorBookData}} -->
        <!-- {{ErrorBookData}} -->
        <ErrorContentArea @changeAnswerGroups="changeAnswerGroupsFn" :AnswerGroups="AnswerGroups"
            :DriveanswerData="DriveanswerData"></ErrorContentArea>
            <div class="footer">
                  <Errorfooter  @changeAnswerGroups="changeAnswerGroupsFn"
            :AnswerGroups="AnswerGroups"></Errorfooter>
            </div>
      
    </div>
</template>
<style lang="less">
    .errortestcontenttest{
        position: relative;
    }
</style>
<script>
    import Errorfooter from './Errorfooter'
    import ErrorContentArea from './ErrorContentArea'
    export default {
        name: 'errortestcontenttest',
        props: ['ErrorBookData', 'DriveanswerData'],
        components: {
            Errorfooter,
            ErrorContentArea
        },
        data() {
            return {
                AnswerGroups: {
                    Answertrue: 0, //答对题目数量
                    Answerfalse: 0, //答错题目数量
                    Answerno: 0, //当前答题数量
                    ErrorNum: window.localStorage.ErrorNum, //在本地存储获取错误题目总数量
                }
    
            }
        },
        methods: {
            changeAnswerGroupsFn(keyname) {
                this.AnswerGroups[keyname]++
            }

        },
    }
</script>