<template>
    <div class="errorcontentarea">
        <div class="content">
            <div class="test-title">
                <div class="test-icon">
                    <span v-if="answerLength.length===4">选择题</span>
                    <span v-else>判断题</span>
                </div>
                <span class="test-question">
                    {{nowdate.question}}
                </span>
            </div>

            <div v-if="nowdate.url" class="test-img">
                <img :src="nowdate.url">
            </div>

            <div class="answerb">
                <li v-for="(item, index) in answerLength" :key="index">
                    <div v-if="item" class="option">
                        <span :class="[errornum===index+1?'error':'']" @click="checkanswerfn(index+1)" class="option-item">{{indexAz[index+1]}}</span>
                        <span class="chose-question">{{item}}</span>
                    </div>
                </li>
            </div>

            <div v-if="alertErrorActive" style="margin-top:3rem" class="alertError">
                <hr>
                <h1>正确答案:{{indexAz[this.DriveanswerData.result[this.ErrorBookData[this.AnswerGroups.Answerno]].answer]}}</h1>
                <p
                    style="line-height:2.5rem;word-break:break-all;font-size:1.5rem;letter-spacing:0.3rem;text-indent:2rem">
                    {{nowdate.expalins}}</p>
                <button @click="btnnext"
                    style="position:fixed;top:50%;right:0%;padding:0.5rem 3rem;border:none;border-radius:2rem;font-weight:bold;background:green;color:white;">下一题</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props: ['DriveanswerData', 'AnswerGroups'],
        name: 'errorcontentarea',
        data() {
            return {
                //错误题目Id编号
                ErrorBookData: window.localStorage.ErrorBook.split(','),
                dataGroups: this.DriveanswerData,

                alertErrorActive: false,
                errornum: null,

                answer: null,
                nowdate: null,
                indexAz: {
                    1: 'A',
                    2: 'B',
                    3: 'C',
                    4: 'D'
                },
                //查看是否选择题
                answerLength: [],
            }
        },
        methods: {
            btnnext() {
                this.alertErrorActive = false
                this.errornum=null
                // 当答题完成时执行
                if (this.AnswerGroups.Answerno === this.ErrorBookData.length - 1) {
                    this.$router.push('/errorbook/errorcontent')
                    return
                }
                this.$emit('changeAnswerGroups', 'Answerno')
            },
            checkanswerfn(answerchose) {
                //   console.log(answerchose)
                if (this.alertErrorActive) {
                    return
                }
                if (answerchose == this.DriveanswerData.result[this.ErrorBookData[this.AnswerGroups.Answerno]].answer) {
                  
               
                    //将答对的题目进行本地删除
                    let ErrorBook = window.localStorage.ErrorBook.split(',')
                    //数组错误ID拷贝
                    let newError = [];
                    for (let index = 0; index < ErrorBook.length; index++) {
                        newError.push(ErrorBook[index])
                    }
                    console.log(ErrorBook)
                    //查找当前索引号
                    let Errorindex = ErrorBook.indexOf(this.DriveanswerData.result[this.ErrorBookData[this.AnswerGroups
                        .Answerno] - 1].id.toString())
                    //删除数组
                    newError.splice(Errorindex, 1)
                    window.localStorage.ErrorBook = newError.toString()
                    window.localStorage.ErrorNum = Number(window.localStorage.ErrorNum) - 1
                    this.$emit('changeAnswerGroups', 'Answertrue')
                } else {
                    this.alertErrorActive = true
                this.errornum=null                    
                    console.log('答题错误')
                        this.errornum=answerchose
                    this.$emit('changeAnswerGroups', 'Answerfalse')
                    return
                }
                // 当答题完成时执行
                if (this.AnswerGroups.Answerno === this.ErrorBookData.length - 1) {
                    this.$router.push('/errorbook/errorcontent')
                    return
                }
                this.$emit('changeAnswerGroups', 'Answerno')
            }
        },
        created() {
            this.nowdate = this.DriveanswerData.result[this.ErrorBookData[this.AnswerGroups.Answerno]]
            for (let key in this.nowdate) {
                if (key.match('item')) {
                    this.answerLength.push(this.nowdate[key])
                }
            }
        },
        watch: {
            'AnswerGroups.Answerno'() {
                this.answerLength.length = [] //清空数组
                this.nowdate = this.DriveanswerData.result[this.ErrorBookData[this.AnswerGroups.Answerno]]
                for (let key in this.nowdate) {
                    if (key.match('item')) {
                        this.answerLength.push(this.nowdate[key])
                    }
                }
            }
        },
    }
</script>

<style scope lang="less">
    .errorcontentarea {
        margin-bottom: 5rem;
        display: flex;
        flex-wrap: wrap;

        .content {
            padding: 2rem;
            width: 100%;

            .test-title {
                font-size: 1.8rem;

                .test-icon {
                    display: inline-block;
                    font-size: 1.2rem;
                    background: yellowgreen;
                    border-top-left-radius: 20rem;
                    border-bottom-left-radius: 20rem;
                    border-top-right-radius: 20rem;
                    padding: 0.5rem 1rem;
                    margin-right: 1rem;

                    span {
                        color: white;
                    }
                }
            }

            .test-img {
                width: 100%;
                display: flex;
                justify-content: center;
                margin: 2rem 0;

                img {
                    max-width: 100%;
                }
            }

            .answerb {
                display: flex;
                flex-wrap: wrap;
                margin-top: 3rem;

                li {
                    width: 100%;
                    margin: 1rem 0;

                    .option {
                        font-size: 2rem;

                        .option-item {
                            &.error{
                                background: red;
                            }
                            padding: 0.5rem;
                            border-radius: 50%;
                            box-shadow: 0 0 0.5rem 0.2rem lightgray;
                            cursor: pointer;
                        }

                        .chose-question {
                            line-height: 2;
                            margin-left: 2rem;
                        }
                    }
                }
            }

        }
    }
</style>